document.addEventListener('DOMContentLoaded', function () {
  // Define a dictionary of variant IDs and their unique pre-order messages
  var preOrderMessages = {
    // Example variant ID and its message (replace with your own)
    // 42544313237725: 'Shipping from 20th May 2024.',
    // LIVE
    42544313237725: 'Shipping from 20th May 2024.',
    39700375076926: 'Shipping from 25th May 2024.',
  };

  var defaultPreOrderMessage = 'Shipping soon!'; // Default message if no unique message is set

  // Select all product forms on the page
  var productForms = document.querySelectorAll('product-form');

  console.log('Product Forms Found:', productForms);

  productForms.forEach(function (form) {
    console.log('Processing Form:', form);
    var variantIdInput = form.querySelector('input.product-variant-id');
    console.log('Variant ID Input:', variantIdInput);
    var submitButton = form.querySelector(
      'product-form button[type="submit"] span',
    );

    if (variantIdInput && submitButton) {
      var variantId = variantIdInput.value;
      // Check if the product is out of stock and should continue selling
      var isPreOrder =
        form.querySelector('input[name="id"]').value === variantId;

      if (isPreOrder) {
        // Change the button text to 'Pre-order'
        submitButton.textContent = 'Pre order';

        // Determine the correct pre-order message to display
        var preOrderMessage =
          preOrderMessages[variantId] || defaultPreOrderMessage;

        // Create and insert the pre-order message element
        var messageElement = document.createElement('span');
        messageElement.className = 'hrm-pre-order-notice';
        messageElement.innerHTML = '<small>' + preOrderMessage + '</small>';

        // Append the message element after the product form
        form.parentNode.insertBefore(messageElement, form.nextSibling);
      }
    }
  });
});
